import React from 'react';
import Link from 'next/link';
import styles from '../styles/Home.module.css'; // Importando o CSS da Home

const Home: React.FC = () => {
  return (
    <div className={styles.container}>
      <h1>Bem-vindo ao Sistema de Reservas</h1>
      <p>Faça login ou registre-se para começar.</p>
      <div>
        <Link href="/login">
          <a className={styles.link}>Login</a>
        </Link>
        <Link href="/register">
          <a className={styles.link}>Registrar</a>
        </Link>
      </div>
    </div>
  );
};

export default Home;
